<template>

</template>
<script>
  import eventBus from './eventBus'
  import notificationHandler from './notificationHandler'
  export default {
    data() {
      return {
        interval: 10000
      }
    },
    methods: {},
    props: ["notes", "notes_ready"],
    created() {

    },
    watch: {
      notes_ready(newValue, oldValue) {
        if (newValue === true) {
          setInterval(() => {
//            console.log("notes" + this.notes.length);
            //notes,
            for (let i in this.notes) {
              if (this.notes[i].informTime !== "") {
                let now = new Date();
                let informTime = new Date(this.notes[i].informTime);
                let diff = Math.abs(now - informTime);
                if (diff < this.interval && this.notes[i].isInformed ==="false") {
                  console.log("" + diff/1000 + "");
                  notificationHandler.showNotification("", this.notes[i].title);
                  this.notes[i].isInformed = "true";
                  let data = new FormData();
                  data.append("isInformed", "true");
                  this.axios.post(this.GLOBAL.server_address + '/note/update/' + this.note._id, data)
                    .then((response) => {
                      console.log("");
                      console.log(response);
//                      this.$emit("refreshThis");
                    })
                    .catch((e) => {
                      console.log('');
                      console.log(e);
                    })
                }
              }
            }
            console.log("--------------------loop-------------------------")

          }, this.interval)
        }
      }
    }
  }
</script>
